import React from 'react';
import Customer from './components/Customer';
import Owner from './components/Owner';
import EventManager from '../../EventManager.js';

import './css/business.css';

class Business extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      type: 'owner', // owner, customer
      business: {
        id: '0011',
        title: 'Arcadius Business Center',
        tax: '13',
        is_owned: false,
        name_business: 'Fleeca Bank',
        name_owner: 'Nobody',

        left_button: 'null', //'buy', 'washmoney', 'null'

        statistics: {
          profit: {
            day: '1500',
            week: '15000',
            month: '150000',
          },
          top_product: {
            0: { id: '0', name: 'Turbine', count: '200' },
            1: { id: '1', name: 'Back light', count: '100' },
            2: { id: '2', name: 'Screwdriver', count: '50' },
          },
        },

        settings: {
          card_account: { number: '9999999999999999' },
          product_tax: { value: '125' },
          bank: { id: 0 },
          font: { id: 0 },
          color: { id: 0 },
          opacity: { value: 50 },
          office: { id: 0 },
        },
        bank: {
          list: [
            { name: 'Fleeca', tax: '3' },
            { name: 'Blane County', tax: '5' },
            { name: 'Pacific Standart', tax: '10' },
            { name: 'Maze Bank', tax: '15' },
          ],
        },
      },
      transactions: [],
    };
  }

  componentDidMount() {
    EventManager.addHandler('businessMenu', (value) => {
      if (value.type === 'show') {
        this.setState({ show: true });
      } else if (value.type === 'hide') {
        this.setState({ show: false });
      } else if (value.type === 'updateBusiness') {
        //console.log(value.businessData);
        this.updateBusiness(value.businessData.type, value);
      }
    });
  }

  updateBusiness(type, value) {
    console.log(type, value.businessData);
 
    this.setState({ type: `${value.businessData.type}` });

    
    switch (type) {
      case 'owner': {
        // this.setState({ transactions: JSON.parse(value.businessData.logs) });
        this.setState((prev) => ({
          business: {
            ...prev.business,
            id: `${value.businessData.bID}`,
            name_owner: value.businessData.ownerName,
            name_business: value.businessData.name,
            tax: value.businessData.profitTax,
            is_owned: value.businessData.isOwned,
            statistics: {
              ...prev.business.statistics,
              profit: {
                ...prev.business.statistics.profit,
                day: `${value.businessData.dailyStat}`,
                week: `${value.businessData.weeklyStat}`,
                month: `${value.businessData.monthlyStat}`,
              }
            },
            settings: {
              ...prev.business.settings,
              card_account: {
                ...prev.business.settings.card_account,
                number: value.businessData.settings.cardnumber,
              },
              product_tax: {
                ...prev.business.settings.product_tax,
                value: value.businessData.settings.productPrices,
              },
              bank: {
                ...prev.business.settings.bank,
                id: value.businessData.settings.bankID,
              },
              font: {
                ...prev.business.settings.font,
                id: value.businessData.settings.font,
              },
              color: {
                ...prev.business.settings.color,
                id: value.businessData.settings.color,
              },
              opacity: {
                ...prev.business.settings.opacity,
                id: value.businessData.settings.opacity,
              },
              office: {
                ...prev.business.settings.office,
                id: value.businessData.settings.office,
              },
            },
            bank: {
              ...prev.business.bank.list,
              list: value.businessData.bankList,
            },
          },
        }));
        break;
      }
      case 'customer': {
        if (value.businessData.leftButton === 'buy') {
          this.setState((prev) => ({
            business: {
              ...prev.business,
              id: `${value.businessData.bID}`,
              name_owner: value.businessData.ownerName,
              name_business: value.businessData.name,
              tax: value.businessData.profitTax,
              is_owned: value.businessData.isOwned,
              left_button: `${value.businessData.leftButton}`,
            },
          }));
        } else if (value.businessData.leftButton === 'washmoney') {
          this.setState((prev) => ({
            business: {
              ...prev.business,
              id: `${value.businessData.bID}`,
              name_owner: value.businessData.ownerName,
              name_business: value.businessData.name,
              tax: value.businessData.profitTax,
              is_owned: value.businessData.isOwned,
              left_button: `${value.businessData.leftButton}`,
            },
          }));
        } else {
          this.setState((prev) => ({
            business: {
              ...prev.business,
              id: `${value.businessData.bID}`,
              name_owner: value.businessData.ownerName,
              name_business: value.businessData.name,
              tax: value.businessData.profitTax,
              is_owned: value.businessData.isOwned,
              left_button: `${value.businessData.leftButton}`,
            },
          }));
        }
        break;
      }
      default:
        break;
    }
  }
  clickButton(type, data) {
    switch (type) {
      case 'close':
        console.log(type);
        mp.trigger('client:business:closeMenu', parseInt(this.state.business.id)); // eslint-disable-line
        break;
      case 'buybusiness':
        mp.trigger('client:business:buy', parseInt(this.state.business.id)); // eslint-disable-line
        break;
      case 'deposit':
        let bankID = 0;
        switch (data.bank) { //dude.... switching string cases doesn't work in ragemp client side, i don't know what's wrong with it, but I had to do this sending INT instead...
          //crapy crap code, sorry
          case "fleeca": bankID = 1; break;
          case "blaine": bankID = 2; break;
          case "pacific": bankID = 3; break;
          case "maze": bankID = 4; break;
          default: break;
        }
        mp.trigger('client:business:deposit', parseInt(this.state.business.id), bankID, data.card, parseInt(data.amount)); // eslint-disable-line
        break;
      case 'withdraw': {
        //console.log(type, data);
        let bankID = 0;
        switch (data.bank) {
          //crapy crap code, sorry
          case "fleeca": bankID = 1; break;
          case "blaine": bankID = 2; break;
          case "pacific": bankID = 3; break;
          case "maze": bankID = 4; break;
          default: break;
        }
        mp.trigger('client:business:withdraw', parseInt(this.state.business.id), bankID, data.card, parseInt(data.amount)); // eslint-disable-line
        break;
      }
      case 'saveSettings':
        mp.trigger('client:business:saveChanges', parseInt(this.state.business.id)); // eslint-disable-line
        break;
      default:
        break;
    }
  }

  changeSettings(type, value) {
    console.log(type, value);

    this.setState((prev) => ({
      business: {
        ...prev.business,
        settings: {
          ...prev.business.settings,
          [type]: {
            ...prev.business.settings[type],
            ...value,
          },
        },
      },
    }));
    mp.trigger('client:business:changeSettings', parseInt(this.state.business.id), type, value.id); // eslint-disable-line
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='business__position'>
          {this.state.type === 'customer' ? <Customer data={this.state.business} clickButton={this.clickButton.bind(this)} /> : null}
          {this.state.type === 'owner' ? (
            <Owner data={this.state.business} logs={this.state.transactions} clickButton={this.clickButton.bind(this)} changeSettings={this.changeSettings.bind(this)} />
          ) : null}
        </div>
      </React.Fragment>
    );
  }
}

export default Business;
