import React from 'react';

import EventManager from '../../EventManager';

import Box from './components/Box';

import './css/homemenu.css';

class HomeMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      value: '',
      home: {
        id: '0011',
        type: 'resident', // resident ;  buy - house for sale; homesettings - settings for owner
        address: 'Strowberry 1557',
        owner: 'James Lantana',
        max_people_living: '4',
        price: 123123123,
        isOwner: false,
        hasSafePin: false,
        hasDoorPin: false,
        garage: {
          size: '1',
          text: 'Маленький', // small - Маленький; medium - Средний; big - Большой
        },
        img: '1',
        pincode: {
          home: '',
          safe: '',
        },
      },
    };
  }

  componentDidMount() {
    EventManager.addHandler('HomeMenu', (value) => {
      if (value.type === 'show') {
        this.setState({ show: true });
      } else if (value.type === 'hide') {
        this.setState({ show: false });
      } else if (value.type === 'updateMenu') {
        console.log(value);
        //TODO (update home values from client side)
        let data = value.home;
        this.updateHouseData(data);
 
          //updateHouseData
      }
    });
  }

  componentWillUnmount() {
    EventManager.removeHandler('HomeMenu');
  }

  valueInput(e, name) {
    switch (name) {
      case 'home':
        this.setState((prev) => ({
          home: {
            ...prev.home,
            pincode: {
              ...prev.home.pincode,
              home: e,
            },
          },
        }));
        break;

      case 'safe':
        this.setState((prev) => ({
          home: {
            ...prev.home,
            pincode: {
              ...prev.home.pincode,
              safe: e,
            },
          },
        }));
        break;

      default:
        break;
    }
  }
  updateHouseData = (data) => {
      switch (data.type) {
        case 'resident': {
          this.setState( prevState => ({ home: 
            {
              ...prevState.home,
              id: data.id,
              type: data.type,
              address: data.address,
              owner: data.owner,
              max_people_living: data.max_people_living
            }
          }));
          break; 
        }
        case 'buy': {
          this.setState( prevState => ({ home: 
            {
              ...prevState.home,
              id: data.id,
              type: data.type,
              address: data.address,
              price: data.price,
              max_people_living: data.max_people_living,
              garage: {
                ...prevState.home.garage,
                size: data.garagesize,
                text: data.garagetext,
              }
            }
          }));
        
          break;
        }
        case 'homesettings': {
          //TODO
          this.setState( prevState => ({ home: 
            {
              ...prevState.home,
              id: data.id,
              type: data.type,
              address: data.address,
              isOwner: data.isOwner,
              hasSafePin: data.hasSafePin,
              hasDoorPin: data.hasDoorPin
            }
          }));
          break;
        }
     
        default: break;
      }

  };
  clickButton(type) {
    switch (type) {
      case 'resident':
        mp.trigger('client:homemenu:enter', this.state.home.id); // eslint-disable-line

        break;
      case 'buy':
        mp.trigger('client:homemenu:buy', this.state.home.id); // eslint-disable-line
        break;

      case 'homesettings':
        mp.trigger('client:homemenu:homesettings', this.state.home.id, this.state.home.pincode); // eslint-disable-line

        break;

      case 'entergarage':
        mp.trigger('client:homemenu:entergarage', this.state.home.id, this.state.home.pincode); // eslint-disable-line

        break;
      case 'checkhome':
        mp.trigger('client:homemenu:checkhome', this.state.home.id); // eslint-disable-line

        break;

      case 'unlockhouse':
        mp.trigger('client:homemenu:unlockhome', this.state.home.id); //eslint-disable-line
        break;
      case 'lockhouse': 
        mp.trigger('client:homemenu:lockhome', this.state.home.id); //eslint-disable-line
        break;

      default:
        break;
    }
  }
  closeHome() {
    mp.trigger('client:homemenu:closeHome'); // eslint-disable-line
  }

  render() {
    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className='home__position'>
          <div className='home__main'>
            <div className='home__main_img' style={{ backgroundImage: `url(${require(`./img/home_bg/${this.state.home.img}.png`)})` }}></div>
            <Box data={this.state.home} valueInput={this.valueInput.bind(this)} clickButton={this.clickButton.bind(this)} />

            <div className='home__main_box_buttons'>
              <div className='home__main_box_button' onClick={() => this.clickButton(this.state.home.type)}>
                {this.state.home.type === 'resident' ? 'Войти' : null}
                {this.state.home.type === 'buy' ? 'Купить' : null}
                {this.state.home.type === 'homesettings' ? 'Выйти из дома' : null}
              </div>
              <div className='home__main_box_button' onClick={() => this.closeHome()}>
                Закрыть
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default HomeMenu;
