import React from 'react';

class Statistics extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className='business__main_stat'>
          <div className='business__main_stat_boxes'>
            <div className='business__main_stat_boxes_box'>
              <div className='business__main_stat_boxes_box_img'></div>
              <div className='business__main_stat_boxes_box_text'>
                <div className='business__main_stat_boxes_box_text_note'>В день</div>
                <div className='business__main_stat_boxes_box_text_money'>{this.props.data.profit.day.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}$</div>
              </div>
            </div>
            <div className='business__main_stat_boxes_box'>
              <div className='business__main_stat_boxes_box_img'></div>
              <div className='business__main_stat_boxes_box_text'>
                <div className='business__main_stat_boxes_box_text_note'>В неделю</div>
                <div className='business__main_stat_boxes_box_text_money'>{this.props.data.profit.week.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}$</div>
              </div>
            </div>
            <div className='business__main_stat_boxes_box'>
              <div className='business__main_stat_boxes_box_img'></div>
              <div className='business__main_stat_boxes_box_text'>
                <div className='business__main_stat_boxes_box_text_note'>В месяц</div>
                <div className='business__main_stat_boxes_box_text_money'>{this.props.data.profit.month.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}$</div>
              </div>
            </div>
          </div>
          <div className='business__main_stat_title'>
            <div className='business__main_stat_title_box'>Топ популярных товаров</div>
            <div className='business__main_stat_note_box'>Статистика за месяц</div>
          </div>
          <div className='business__main_stat_top'>
            <div className='business__main_stat_top_box top_gold'>
              <div className='business__main_stat_top_box_name'>
                {this.props.data.top_product[0].name} <div className='business__main_stat_top_box_vector'></div>{' '}
              </div>
              <div className='business__main_stat_top_box_note'>Было продано более {this.props.data.top_product[0].count} раз</div>
            </div>
            <div className='business__main_stat_top_box top_silver'>
              <div className='business__main_stat_top_box_name'>
                {this.props.data.top_product[1].name} <div className='business__main_stat_top_box_vector'></div>{' '}
              </div>
              <div className='business__main_stat_top_box_note'>Было продано более {this.props.data.top_product[1].count} раз</div>
            </div>
            <div className='business__main_stat_top_box top_bronze'>
              <div className='business__main_stat_top_box_name'>
                {this.props.data.top_product[2].name} <div className='business__main_stat_top_box_vector'></div>{' '}
              </div>
              <div className='business__main_stat_top_box_note'>Было продано более {this.props.data.top_product[2].count} раз</div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Statistics;
