import React from 'react';

class Box extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        {this.props.data.type === 'resident' ? (
          <div className='home__main_grid'>
            <div className='home__main_box_info'>
              <div className='home__main_box_info_note'>Адрес:</div>
              <div className='home__main_box_info_text'>{this.props.data.address}</div>
            </div>
            <div className='home__main_box_info'>
              <div className='home__main_box_info_note'>Владелец:</div>
              <div className='home__main_box_info_text'>{this.props.data.owner}</div>
            </div>
            <div className='home__main_box_info'>
              <div className='home__main_box_info_note'>Кол-во жилых мест:</div>
              <div className='home__main_box_info_text'>{this.props.data.max_people_living}</div>
            </div>
            <div className='home__main_box_info'>
              <div className='home__main_box_info_note'>Кол-во гаражей:</div>
              <div className='home__main_box_info_text'>{this.props.data.garage.size}</div>
            </div>
          </div>
        ) : null}
        {this.props.data.type === 'buy' ? (
          <div className='home__main_grid'>
            <div className='home__main_box_info'>
              <div className='home__main_box_info_note'>Адрес:</div>
              <div className='home__main_box_info_text'>{this.props.data.address}</div>
            </div>
            <div className='home__main_box_info'>
              <div className='home__main_box_info_note'>Кол-во жилых мест:</div>
              <div className='home__main_box_info_text'>{this.props.data.max_people_living}</div>
            </div>

            <div className='home__main_grid4'>
              <div className='home__main_box_info'>
                <div className='home__main_box_info_note'>Кол-во гаражей:</div>
                <div className='home__main_box_info_text'>{this.props.data.garage.size}</div>
              </div>
              <div className='home__main_box_info'>
                <div className='home__main_box_info_note'>Гараж</div>
                <div className='home__main_box_info_text'>{this.props.data.garage.text}</div>
              </div>
              <div className='home__main_box_info'>
                <div className='home__main_box_info_note'>Цена</div>
                <div className='home__main_box_info_text'>{this.props.data.price}</div>
              </div>
              <div className='home__main_box_info_button' onClick={() => this.props.clickButton('checkhome')}>
                Осмотреть дом
              </div>
            </div>
          </div>
        ) : null}
        {this.props.data.type === 'homesettings' ? (
          <div className='home__main_grid'>
            <div className='home__main_box_info'>
              <div className='home__main_box_info_note'>Адрес:</div>
              <div className='home__main_box_info_text'>{this.props.data.address}</div>
            </div>
            {
                this.props.data.isOwner === true ? (
                  <React.Fragment>
                    {
                      this.props.data.hasDoorPin === true ? (
                        <div className='home__main_box_input'>
                          <input
                            type='text'
                            placeholder='Сменить пин-код'
                            className='home__main_box_info_input'
                            maxLength='8'
                            value={this.props.data.pincode.home}
                            onChange={(e) => this.props.valueInput(e.target.value.replace(/\D/, ''), 'home')}
                          />
                        </div>
                      ) : null
                    } {
                      this.props.data.hasSafePin === true ? (
                        <div className='home__main_box_input'>
                            <input
                              type='text'
                              placeholder='Сменить пин-код от сейфа'
                              className='home__main_box_info_input'
                              maxLength='8'
                              value={this.props.data.pincode.safe}
                              onChange={(e) => this.props.valueInput(e.target.value.replace(/\D/, ''), 'safe')}
                            />
                        </div>
                      ) : null
                    }
                </React.Fragment>
              ) : null
           }
            <div className='home__main_box_input' onClick={() => this.props.clickButton('entergarage')}>
              Войти в гараж
            </div>

            {
              this.props.data.isOwner === true ? (
                <div className="home__main_grid4">
                <div className='home__main_box_button' style={{ background: 'green', height: 30, cursor: 'pointer' }} onClick={() => this.props.clickButton('unlockhouse')}>
                Открыть дверь
                </div>
                <div className='home__main_box_button' style={{ background: 'red', height: 30, cursor: 'pointer'}} onClick={() => this.props.clickButton('lockhouse')}>
                Закрыть дверь
                </div>
              </div>
              ) : null
            }
          </div>
        ) : null}
      </React.Fragment>
    );
  }
}

export default Box;
