import React from 'react';
import '../css/general.css';

const Card = ({ icon, title, subtitle }) => {
  return (
    <div className='accountmenu__content__cards__finance__card'>
      <div
        className={`${icon === 'card' ? `accountmenu__card__icon2` : `accountmenu__card__icon`}`}
        style={{
          backgroundImage:
            icon === 'card'
              ? `url(https://os-rp.com/state99/client/images/icons/components/accountmenu/img/card.svg)`
              : `url(https://os-rp.com/state99/client/images/icons/components/accountmenu/img/wallet-alt.svg)`,
        }}
      ></div>
      <span className='accountmenu__content__cards__finance__card__title'>{title}</span>
      <span className='accountmenu__content__cards__finance__card__subtitle'>{subtitle}</span>
    </div>
  );
};

export default Card;
