import React from 'react';
import '../css/property.css';
import LineData from '../uikit/LineData';
import ButtonGps from '../uikit/ButtonGps';
import BusinessCard from '../uikit/BusinessCard';
import CarCard from '../uikit/CarCard';

const Property = ({ house, business, cars }) => {
  return (
    <React.Fragment>
      <div className='accountmenu__content__cards__item_fix_property accountmenu__content__cards1'>
        <div className='accountmenu__content__cards__item__container '>
          <div className='accountmenu__content__cards__item__house__img__container'>
            <div
              className='accountmenu__content__cards__item__container__house__img'
              style={{ backgroundImage: `url(https://os-rp.com/state99/client/images/icons/components/accountmenu/img/house_img_bg.svg)` }}
            ></div>
          </div>
          <div className='accountmenu__content__cards__house'>
            <div className='accountmenu__content__cards__house__address'>
              <span className='accountmenu__content__cards__house__type'>{house.type}</span>
              <span className='accountmenu__content__cards__house__address__name'>{house.address}</span>
            </div>
            <div className='accountmenu__content__cards__house__address2'>
              <div className='accountmenu__content__cards__house__box'>
                <span className='accountmenu__content__cards__house__name'>{house.name}</span>
                <span className='accountmenu__content__cards__house__address__text'>Адрес</span>
              </div>
              <div style={{ marginLeft: 'auto' }}>
                <ButtonGps filled={false} x={house.x} y={house.y} />
              </div>
            </div>
            <LineData leftinfo='Налог' rightinfo={house.doors} />
            <LineData leftinfo='Жилых мест' rightinfo={house.roommate} />
            <LineData leftinfo='Гараж' rightinfo={house.carplace} />
            <div className='accountmenu__content__cards__house__hprice'>
              <div className='accountmenu__content__cards__house__hprice__sell'>
                <div className='accountmenu__content__cards__house__hprice__sell__linebtn'>
                  <div className='accountmenu__content__cards__house__hprice__sell__icon' />
                  <span className='accountmenu__content__cards__text'>Гос. цена:</span>
                  <span className='accountmenu__content__cards__house__hprice__sell__text'>{`${house.gprice.replace(/\B(?=(\d{3})+(?!\d))/g, '.')} $`}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className='accountmenu__content__cards__item_fix_property accountmenu__content__cards2'>
        <div className='accountmenu__content__cards__container_property'>
          <div className='accountmenu__content__cars__header__container'>
            <div className='accountmenu__content__cards__title_medium'>Имущество</div>
            <div className='accountmenu__content__cards__title__count'>{`Кол-во: ${business.length}`}</div>
          </div>
          <div className='accountmenu__cards__question__container_property accountmenu__scrollable'>
            {business.map((item, index) => (
              <BusinessCard {...item} key={`business-list-` + index} />
            ))}
          </div>
        </div>
      </div>
      <div className='accountmenu__content__cards__item_fix_property accountmenu__content__cards3 '>
        <div className='accountmenu__content__cards__container_property'>
          <div className='accountmenu__content__cars__header__container'>
            <span className='accountmenu__content__cards__title_medium'>Транспорт</span>
            <span className='accountmenu__content__cards__title__count'>{`Кол-во: ${cars.length} / 10`}</span>
          </div>
          <div className='accountmenu__cards__question__container_property accountmenu__scrollable'>
            {cars.map((item, index) => (
              <CarCard {...item} key={`car-list-` + index} />
            ))}
          </div>
        </div>
        {/*<div className="accountmenu__content__cards__headerts" style={{paddingLeft: '5.185185185185185vh', paddingRight: '5.185185185185185vh', marginTop: '5%', marginBottom: '5%'}}>
                    <span className="accountmenu__content__cards__title">
                        Транспорт
                    </span>
                    <span className="accountmenu__content__cards__title__count">
                        {`кол-во: ${cars.length} / 10`}
                    </span>
                </div>
                <div className="accountmenu__content__cards__cars__list">
                    {cars.map((item, index) => (
                        <CarCard
                            {...item}
                            key={`car-list-` + index}
                        />
                    ))}

                    </div>*/}
      </div>
    </React.Fragment>
  );
};

export default Property;
