import React from 'react';

import { CSSTransition } from 'react-transition-group';

class Deposit extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      select_bank: '',
      value_number: '',
      value_amount: '',
      bank: {
        fleeca: {
          name: 'Fleeca Shield',
        },
        blaine: {
          name: 'Blaine Protection system',
        },
        pacific: {
          name: 'Pacific Security',
        },
        maze: {
          name: 'Maze SEC',
        },
      },
    };
    this.nodeRef = React.createRef();
    this.nodeRef2 = React.createRef();
  }

  render() {
    return (
      <React.Fragment>
        <div className='business__deposit_main'>
          <div className='business__deposit_main_bank'>
            <div className='business__deposit_main_bank_box'>
              <div className='business__deposit_main_bank_box_img fleeca_bank'></div>
              <input type='radio' id='checkbank1' name='checkbank' checked={this.state.select_bank === 'fleeca'} onChange={() => this.setState({ select_bank: 'fleeca' })} />
              <label htmlFor='checkbank1' className='business__deposit_main_bank_box_check'></label>
            </div>
            <div className='business__deposit_main_bank_box'>
              <div className='business__deposit_main_bank_box_img blaine_bank'></div>
              <input type='radio' id='checkbank2' name='checkbank' checked={this.state.select_bank === 'blaine'} onChange={() => this.setState({ select_bank: 'blaine' })} />
              <label htmlFor='checkbank2' className='business__deposit_main_bank_box_check'></label>
            </div>
            <div className='business__deposit_main_bank_box'>
              <div className='business__deposit_main_bank_box_img pacific_bank'></div>
              <input type='radio' id='checkbank3' name='checkbank' checked={this.state.select_bank === 'pacific'} onChange={() => this.setState({ select_bank: 'pacific' })} />
              <label htmlFor='checkbank3' className='business__deposit_main_bank_box_check'></label>
            </div>
            <div className='business__deposit_main_bank_box'>
              <div className='business__deposit_main_bank_box_img maze_bank'></div>
              <input type='radio' id='checkbank4' name='checkbank' checked={this.state.select_bank === 'maze'} onChange={() => this.setState({ select_bank: 'maze' })} />
              <label htmlFor='checkbank4' className='business__deposit_main_bank_box_check'></label>
            </div>
          </div>
          <CSSTransition nodeRef={this.nodeRef} in={this.state.select_bank !== ''} timeout={900} classNames='fade_zone' unmountOnExit>
            <div ref={this.nodeRef}>
              <div className='business__deposit_main_bank_note'>{this.props.data.text}</div>
              <div className='business__deposit_main_bank_clm'>
                <div className='business__deposit_main_bank_clm_left'>
                  <input
                    type='text'
                    className='business_deposit_input'
                    placeholder='Введите номер счета'
                    value={this.state.value_number}
                    onChange={(e) => this.setState({ value_number: e.target.value.replace(/[^0-9.]/g, '') })}
                  />
                  <input
                    type='text'
                    className='business_deposit_input'
                    placeholder='Введите сумму'
                    value={this.state.value_amount}
                    onChange={(e) => this.setState({ value_amount: e.target.value.replace(/[^0-9.]/g, '') })}
                  />
                  <CSSTransition nodeRef={this.nodeRef2} in={this.state.value_number !== '' && this.state.value_amount !== ''} timeout={900} classNames='fade_zone' unmountOnExit>
                    <div
                      className='business_deposit_button'
                      ref={this.nodeRef2}
                      onClick={() => this.props.clickButton(this.props.data.id, { bank: this.state.select_bank, card: this.state.value_number, amount: this.state.value_amount })}
                    >
                      {this.props.data.text}
                    </div>
                  </CSSTransition>
                </div>
                <div className='business__deposit_main_bank_clm_right'>
                  <div className='business__deposit_main_bank_clm_right_text1'> {this.state.select_bank !== '' ? this.state.bank[this.state.select_bank].name : null}</div>
                  <div className='business__deposit_main_bank_clm_right_text2'>Protection of your money</div>
                  <div
                    className='business__deposit_main_bank_clm_right_img'
                    style={{
                      backgroundImage:
                        this.state.select_bank !== '' ? `url(${require(`../../img/icons_sec_bank/${this.state.select_bank}.${this.state.select_bank === 'pacific' ? 'png' : 'svg'}`)})` : null,
                    }}
                  ></div>
                </div>
              </div>
            </div>
          </CSSTransition>
        </div>
      </React.Fragment>
    );
  }
}

export default Deposit;
