import React from 'react';

class Info extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className='business__info_main'>
        У продуктов есть свой отдельный счет, если он равен меньше 1$, то бизнес перестает функционировать и нести прибыль. Достаточно пополнять счет продуктов, чтобы бизнес функционировал. Цена каждого продукта уникальна, она равна минимальной стоимости наценки на товар. Поэтому если вы ставите цену на товар в 100%, то ваш бизнес будет работать в ноль. Если у бизнеса нет возможности ставить наценку, то текущая цена на продукт делённая на два.
          <div className='business__info_main_img'></div>
        </div>
      </React.Fragment>
    );
  }
}

export default Info;
