import React from 'react';
import parseText from '../functions/parseText';
import '../main.css';

const styles = {
  header: {
    fontFamily: 'HACKED',
    color: '#fff',
    fontSize: '5.925925925925926vh',
    fontWeight: 400,
    padding: '1.8518518518518519vh 1.8518518518518519vh',
    textAlign: 'center',
    minHeight: '6.018518518518518vh',
  },
  headerDataContainer: {
    display: 'flex',
    flexDirection: 'row',
    padding: '0.9259259259259259vh 1.8518518518518519vh',
    fontFamily: 'Century Gothic',
    color: '#fff',
    fontWeight: 400,
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: '1.4814814814814814vh',
    marginTop: '2.2222222222222223vh',
    marginBottom: '2.2222222222222223vh',
  },
  headerDesc: {
    fontSize: '1.4814814814814814vh',
  },
  headerDescCount: {
    marginLeft: '2.2222222222222223vh',
  },
  banner: {
    width: '100%',
    height: '11.11111111111111vh',
    objectFit: 'cover',
    objectPosition: 'center',
    zIndex: '1',
    borderRadius: '0.9259259259259259vh',
  },
};

export default function Header(props) {
  /*if (props.banner)
    {
        styles.header.backgroundImage = `url(${require('../img/banners/' + props.banner + '.png')})`;
        styles.header.backgroundPosition = 'center';
    }*/

  return (
    <div>
      <div
        className='mainmenu__header__container'
        style={{ minHeight: props.banner ? '11.574074074074074vh' : '', backgroundImage: props.banner ? `url('https://os-rp.com/state99/client/images/banners/${props.banner}.png')` : '' }}
      >
        <span className='mainmenu__header__title'>{props.headerText}</span>
      </div>
      <div style={styles.headerDataContainer}>
        <div style={styles.headerDesc} dangerouslySetInnerHTML={{ __html: parseText(props.headerDesc) }} />
        <div style={styles.headerDescCount}>{props.headerData}</div>
      </div>
    </div>
  );
}
