import React from 'react';

class Customer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className='business__customer_main'>
          <div className='business__customer_img' style={{ backgroundImage: `url(${require(`../img/bg/arcadius.png`)})` }}></div>
          <div className='business__box'>
            <div className='business__box_text'>
              <div className='business__box_text_name'>{this.props.data.title}</div>
              <div className='business__box_text_tax'>Налог на прибыль {this.props.data.tax}</div>
            </div>
            <div className='business__box_text2'>
              <div className='business__box_text_clm'>
                <div className='business__box_text_clm_note'>Наименование фирмы:</div>
                <div className='business__box_text_clm_name'>{this.props.data.name_business}</div>
              </div>
              <div className='business__box_text_clm'>
                <div className='business__box_text_clm_note style_text_right'>Владелец:</div>
                <div className='business__box_text_clm_name style_text_right'>{this.props.data.name_owner}</div>
              </div>
            </div>
            <div className='business__box_text3'>
              {
                this.props.data.left_button === 'buy' ? (
                  <div className='business__box_button hover_yellow' onClick={() => this.props.clickButton('buybusiness')}>
                    Купить Бизнес
                  </div>
                ) : (
                  <div className='business__box_button hover_yellow' onClick={() => this.props.clickButton('washmoney')}>
                    Отмыть деньги
                  </div>
                ) ? ( <div></div> ) : ( <div></div> /* empty div will center the button */)
              }
              <div className='business__box_button hover_red' onClick={() => this.props.clickButton('close')}>
                Закрыть
              </div>
  
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Customer;
