import React from 'react';

import { Route, HashRouter as Router, Redirect } from 'react-router-dom';
import Deposit from './pages/Deposit';
import Info from './pages/Info';
import Settings from './pages/Settings';
import Statistics from './pages/Statistics';
import Transactions from './pages/Transactions';

class Owner extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      path: '/business/statistics',
      select_button: 'statistics',
      menu_button: {
        settings: { id: 'settings', text: 'Настройки бизнеса', link: '/business/settings' },
        transactions: { id: 'transactions', text: 'Список транзакций', link: '/business/transactions' },
        deposit: { id: 'deposit', text: 'Внести средства', note: 'Выберите банк чтобы продолжить', link: '/business/deposit' },
        withdraw: { id: 'withdraw', text: 'Снять Средства', note: 'Выберите банк чтобы продолжить', link: '/business/withdraw' },
        statistics: { id: 'statistics', text: 'Статистика', link: '/business/statistics' },
        info: { id: 'info', text: 'Что такое продукты?', link: '/business/info' },
      },
      transfer_money: {
        deposit: {
          id: 'deposit',
          text: 'Deposit',
        },
        withdraw: {
          id: 'withdraw',
          text: 'Withdraw',
        },
      },
    };
  }

  render() {
    return (
      <React.Fragment>
        <div className='bussines__box_owner'>
          <div className='bussines__box_owner_left'>
            <div className='bussines__box_owner_left_title'>
              {this.state.menu_button[this.state.select_button].text}
              {this.state.menu_button[this.state.select_button].note !== '' && this.state.menu_button[this.state.select_button].note !== undefined ? (
                <div className='bussines__box_owner_left_note'>{this.state.menu_button[this.state.select_button].note}</div>
              ) : null}
            </div>

            <Router>
              <Route exact path='/business/settings'>
                <Settings data={this.props.data} changeSettings={this.props.changeSettings.bind(this)} />
              </Route>
              <Route exact path='/business/transactions'>
                <Transactions data={this.props.logs} />
              </Route>
              <Route exact path='/business/deposit'>
                <Deposit clickButton={this.props.clickButton.bind(this)} data={this.state.transfer_money.deposit} />
              </Route>
              <Route exact path='/business/withdraw'>
                <Deposit clickButton={this.props.clickButton.bind(this)} data={this.state.transfer_money.withdraw} />
              </Route>
              <Route exact path='/business/statistics'>
                <Statistics data={this.props.data.statistics} />
              </Route>
              <Route exact path='/business/info'>
                <Info />
              </Route>
              <Redirect to={this.state.path} push />
            </Router>
          </div>
          <div className='bussines__box_owner_right'>
            <div className='bussines__box_owner_img' style={{ backgroundImage: `url(${require(`../img/bg/arcadius.png`)})` }}></div>
            <div className='bussines__box_owner_info'>
              <div className='bussines__box_owner_title'>
                <div className='business__box_owner_title_name'>{this.props.data.title}</div>
                <div className='business__box_owner_title_tax'>Банковский баланс: ${this.props.data.tax}</div>
              </div>
              <div className='bussines__box_owner_note_info'>
                <div className='bussines__box_owner_note_info_clm'>
                  <div className='business__box_owner_note_info_clm_note'>Наименование фирмы:</div>
                  <div className='business__box_owner_note_info_clm_name'>{this.props.data.name_business}</div>
                </div>
                <div className='bussines__box_owner_note_info_clm2'>
                  <div className='business__box_owner_note_info_clm_note style_text_right'>Владелец:</div>
                  <div className='business__box_owner_note_info_clm_name style_text_right'>{this.props.data.name_owner}</div>
                </div>
              </div>
            </div>
            <div className='business_box_list_button'>
              {Object.values(this.state.menu_button).map((e, i) => {
                let index = `button_business${i}`;
                return (
                  <div className='business_box_list_button_main' key={index}>
                    <input type='radio' id={index} name='button_business' checked={e.id === this.state.select_button} onChange={() => this.setState({ select_button: e.id, path: e.link })} />
                    <label htmlFor={index} className='business_box_list_button_main_style'>
                      {e.text}
                    </label>
                  </div>
                );
              })}
              <div className='business_box_list_button_main' onClick={() => this.props.clickButton('saveSettings')}>
                <div className='business_box_list_button_main_style button_style_bis'>Сохранить настройки</div>
              </div>
              <div className='business_box_list_button_main' onClick={() => this.props.clickButton('close')}>
                <div className='business_box_list_button_main_style button_style_bis'>Закрыть</div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Owner;
