import React from 'react';

export default function Icon(name) {
  const styles = {
    iconContainer: {
      width: '2.2222222222222223vh',
      height: '1.8518518518518519vh',
      display: 'flex',
      alignItems: 'center',
      backgroundSize: 'cover',
    },
  };

  return (
    <div className={name.arrow ? 'list-arrow-icon' : 'iconContainer'} style={styles.iconContainer}>
      <div
        className='img_icon_style'
        style={{ backgroundImage: `url(${require(`../img/icons/${name.name}.png`)})`, width: `${name.size || '1.3888888888888888vh'}`, height: `${name.size || '1.3888888888888888vh'}` }}
      ></div>
    </div>
  );
}
