import React from 'react';

export default function Img(name) {
  const styles = {
    iconContainer: {
      width: '7.407407407407407vh',
      height: '7.407407407407407vh',
      display: 'flex',
      alignItems: 'center',
    },
  };

  return (
    <div className={name.arrow ? 'list-arrow-icon' : 'iconContainer'} style={styles.iconContainer}>
      <div
        className='img_icon_style'
        style={{
          backgroundImage: `url(https://os-rp.com/state99/client/images/icons/hud_a/${name.name}.png)`,
          width: `${name.size || '7.037037037037037vh'}`,
          height: `${name.size || '7.037037037037037vh'}`,
        }}
      ></div>
    </div>
  );
}
