import React from 'react';

class Transactions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className='business__trans_main'>
          <div className='business__trans_main_header'>
            <div className='business__trans_main_header_1'>#</div>
            <div className='business__trans_main_header_2'>Продукт</div>
            <div className='business__trans_main_header_3'>Цена</div>
            <div className='business__trans_main_header_4'>Время</div>
          </div>
          <div className='business__trans_main_list'>
            {this.props.data
              .sort((a, b) => (a.timestamp < b.timestamp ? 1 : -1))
              .map((e, i) => {
                let index = `trans${i}`;
                return (
                  <div className='business__trans_main_list_li' key={index}>
                    <div className='business__trans_main_list_li_1'>{i + 1}</div>
                    <div className='business__trans_main_list_li_2'>{e.name}</div>
                    <div className={`business__trans_main_list_li_3  ${parseInt(e.price) === 0 ? 'trans_color_red' : 'trans_color_green'}`}>
                      {e.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.')}$
                    </div>
                    <div className='business__trans_main_list_li_4'>
                      {new Date(parseInt(e.timestamp) * 1000).toLocaleString('ru-RU', { month: 'numeric', day: 'numeric', year: 'numeric' })}{' '}
                      {new Date(parseInt(e.timestamp) * 1000).toLocaleString('ru-RU', { hour: 'numeric', minute: 'numeric' })}
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Transactions;
