import React from 'react';

class Settings extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      font: {
        list: [{ name: 'ChaletLondon' }, { name: 'HouseScript' }, { name: 'Monospace' }, { name: 'CharletComprime' }, { name: 'Pricedown' }],
      },
      color: {
        list: [
          { name: 'Black' }, 
          { name: 'Red' }, 
          { name: 'Pink' }, 
          { name: 'Purple' },
          { name: 'Deep Purple' },
          { name: 'Indigo' },
          { name: 'Blue' },
          { name: 'Light Blue' },
          { name: 'Cyan' },
          { name: 'Teal' },
          { name: 'Green' },
          { name: 'Light Green' },
          { name: 'Amber' },
          { name: 'Orange' },
          { name: 'Deep Orange' },
          { name: 'Brown' },
          { name: 'Blue Grey' },
          { name: 'Grey' }
        ],
      },
      office: {
        list: [
          { name: 'Old Spice Classical', img: '1' },
          { name: 'Executive Rich', img: '2' },
          { name: 'Executive Cool', img: '3' },
          { name: 'Executive Contrast', img: '4' },
          { name: 'Old Spice Vintage', img: '1' },
          { name: 'Old Spice Warms', img: '1' },
          { name: 'Power Broker Conservative', img: '1' },
          { name: 'Power Broker Polished', img: '1' },
          { name: 'Power Broker Ice', img: '1' },

        ],
      },
    };
  }

  clickLeftArrow(type, isBank = false) {
    let array = isBank === false ? this.state[type].list : this.props.data[type].list;
    let currentIndex = this.props.data.settings[type].id;
    if (currentIndex === 0) {
      currentIndex = array.length - 1;
    } else {
      currentIndex--;
    }
    this.props.changeSettings(type, { id: currentIndex });
  }

  clickRightArrow(type, isBank = false) {
    console.log(this.props.data.bank.list)
    let array = isBank === false ? this.state[type].list : this.props.data[type].list;
 

 
    let currentIndex = this.props.data.settings[type].id;
    if (currentIndex === array.length - 1) {
      currentIndex = 0;
    } else {
      currentIndex++;
    }
    this.props.changeSettings(type, { id: currentIndex });
  }

  render() {
    return (
      <React.Fragment>
        <div className='business__settings_main'>
          <div className='business__settings_main_row_info'>
            <div className='business__settings_main_row_info_left'>
              <div className='business__settings_main_row_info_left_box'>
                <span className='business__settings_main_row_info_left_box_note1'>Название банка</span>
                <span className='business__settings_main_row_info_left_box_note2'>{this.props.data.name_business}</span>
              </div>
              <div className='business__settings_main_row_info_left_box'>
                <span className='business__settings_main_row_info_left_box_note1'>Налог на прибыль:</span>
                <span className='business__settings_main_row_info_left_box_note2'>{this.props.data.tax}</span>
              </div>
            </div>
            <div className='business__settings_main_row_info_left'>
              <div className='business__settings_main_row_info_left_box'>
                <span className='business__settings_main_row_info_left_box_note1'>Ваш счет</span>
                <span className='business__settings_main_row_info_left_box_note2'>{this.props.data.settings.card_account.number}</span>
              </div>
              <div className='business__settings_main_row_info_left_box'>
                <span className='business__settings_main_row_info_left_box_note1'>Цена на все товары:</span>
                <span className='business__settings_main_row_info_left_box_note2'>{this.props.data.settings.product_tax.value}</span>
              </div>
            </div>
          </div>
          <div className='business__settings_main_slider'>
            <div className='business__settings_main_slider_box'>
              <div className='business__settings_main_slider_box_title'>Ваш банк:</div>
              <div className='business__settings_main_slider_box_slider'>
                <div className='business__settings_main_slider_box_slider_arrow margin_arrow filter_white_arrow' onClick={() => this.clickLeftArrow('bank', true)}></div>
                <div className='business__settings_main_slider_box_slider_text'>
                  <span className='slider_box_text'>{this.props.data.bank.list[this.props.data.settings.bank.id].name}</span>
                  <span className='slider_box_note'>( {this.props.data.bank.list[this.props.data.settings.bank.id].tax}% )</span>
                </div>
                <div className='business__settings_main_slider_box_slider_arrow rotate_arrow filter_white_arrow' onClick={() => this.clickRightArrow('bank', true)}></div>
              </div>
            </div>
            <div className='business__settings_main_slider_box'>
              <div className='business__settings_main_slider_box_title'>Шрифт на тарелке:</div>
              <div className='business__settings_main_slider_box_slider'>
                <div className='business__settings_main_slider_box_slider_arrow margin_arrow filter_white_arrow' onClick={() => this.clickLeftArrow('font')}></div>
                <div className='business__settings_main_slider_box_slider_text'>
                  <span className='slider_box_text'>{this.state.font.list[this.props.data.settings.font.id].name}</span>
                </div>
                <div className='business__settings_main_slider_box_slider_arrow rotate_arrow filter_white_arrow' onClick={() => this.clickRightArrow('font')}></div>
              </div>
            </div>
            <div className='business__settings_main_slider_box'>
              <div className='business__settings_main_slider_box_title'>Цвет на планшете:</div>
              <div className='business__settings_main_slider_box_slider'>
                <div className='business__settings_main_slider_box_slider_arrow margin_arrow filter_white_arrow' onClick={() => this.clickLeftArrow('color')}></div>
                <div className='business__settings_main_slider_box_slider_text'>
                  <span className='slider_box_text'>{this.state.color.list[this.props.data.settings.color.id].name}</span>
                </div>
                <div className='business__settings_main_slider_box_slider_arrow rotate_arrow filter_white_arrow' onClick={() => this.clickRightArrow('color')}></div>
              </div>
            </div>
            <div className='business__settings_main_slider_box'>
              <div className='business__settings_main_slider_box_title'>Прозрачность</div>
              <div className='business__settings_main_slider_box_slider'>
                <input
                  type='range'
                  className='business__settings_main_slider_box_slider_range'
                  value={this.props.data.settings.opacity.value}
                  onChange={(e) => this.props.changeSettings('opacity', { value: e.target.value })}
                />
              </div>
            </div>
          </div>
          <div
            className='business__settings_main_background'
            style={{ backgroundImage: `url(${require(`../../img/bg_business/${this.state.office.list[this.props.data.settings.office.id].img}.png`)})` }}
          ></div>
          <div className='business__settings_main_background_slider_position'>
            <div className='business__settings_main_background_slider'>
              <div className='business__settings_main_slider_box_slider_arrow margin_arrow' onClick={() => this.clickLeftArrow('office')}></div>
              <div className='business__settings_main_slider_box_slider_text'>
                <span className='slider_box_text_black'>{this.state.office.list[this.props.data.settings.office.id].name}</span>
              </div>
              <div className='business__settings_main_slider_box_slider_arrow rotate_arrow' onClick={() => this.clickRightArrow('office')}></div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Settings;
